.add-form {
    position: fixed;
    width: 400px;
    height: 300px;
    top: 50%;
    left: 50%;
    background-color: lightgrey;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    padding: 30px 60px;
}


